.home{
    height: 90vh;
    overflow: hidden;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    .head{
        text-align: center;
        color: white;
        height: min-content;
        padding:5px;
        width: 100%;
        margin-top: 0;
        display: block;
        box-shadow: 0 6px 6px -6px black;
        position: relative;
        background-color: darkcyan;
        h1{
            display: inline-block;
        }
        button{
            display: inline-block;
            vertical-align:middle;
            position: absolute;
            right: 10px;
            top:50%;
            background-color: transparent;
            border: 2px solid cyan;
            color: whitesmoke;
            padding: 3px;

            transform: translateY(-50%);
            box-sizing: border-box;
            &:active {
                box-shadow: 2px 2px 5px black;
                color: cyan;
            }
        }
    }
    .controller{
        position: fixed;
        bottom: 4px;
        width:100%;
        z-index: 1;
        display: flex;
        .SearchBox{
            margin-left: 10px;
            width: 80%;
        }
        button {
            margin-left: auto;
            margin-right: 5px;
            display: inline-block;
            vertical-align:middle;
            position: absolute;
            right: 10px;
            top:50%;
            border: 2px solid cyan;
            cursor: pointer;
            border-radius: 6px;
            color: black;
            padding: 3px;

            transform: translateY(-50%);
            box-sizing: border-box;
            &:active {
                box-shadow: 2px 2px 5px black;
                color: cyan;
            }
        }
    }
    .wrapper_chat{
        height: 100%;
        overflow: hidden;
        padding-top: 20px;
        padding-bottom: 50px;
        .chats{
            background-color: whitesmoke;
            height: 100%;
            overflow-y: scroll;
            // padding: 100px;
            &::-webkit-scrollbar{
                width: 0px;
            }
        }
    }
}

